import {ExtractPropTypes} from "@vue/composition-api/dist/component/componentProps";
import {StyleProps} from "@/use/useStyle";

export const TableProps = {
    ...StyleProps,

    /*---------------------------------------basic-------------------------------------------*/
    data: {type: Array},                                        // 
    virtual: {type: Boolean, default: false},                   // 
    summaryData: {type: Array},                                 // 
    config: {type: Function},                                   // 
    debugPlc: {type: Boolean},                                  // plc
    rules: {type: Object},                                      // 
    colDraggable: {type: Boolean},                              // 

    /*---------------------------------------theme-------------------------------------------*/
    border: {type: Boolean},                                    // 
    // stripe: {type: Boolean},                                    // table
    headRowHeight: {type: [String, Number], default: 45},       // 
    bodyRowHeight: {type: [String, Number], default: 40},       // 
    // hideHeader: {type: Boolean},                                // 
    showRows: {type: Number, default: 10},                      // 
    emptyText: {type: String},                                  // 

    /*---------------------------------------style and class-------------------------------------------*/
    // class style
    // rowClassFunc: {type: Function},                             //  className 
    // rowStyleFunc: {type: Function},                             //  style
    // cellClassFunc: {type: Function},                            //  className 
    // cellStyleFunc: {type: Function},                            //  style 
    // headRowClassFunc: {type: Function},                         //  className 
    // headRowStyleFunc: {type: Function},                         //  style 
    // headCellClassFunc: {type: Function},                        //  className 
    // headCellStyleFunc: {type: Function},                        //  style 
    // rowspanFunc: {type: Function},                              // 
    // colspanFunc: {type: Function},                              // 

    /*---------------------------------------tree-------------------------------------------*/
    lazy: {type: Boolean},                                      // 
    according: {type: Boolean},                                 // 
    loading: {type: Boolean, default: null},                    // 

    keyField: {type: String, required: true},                   // 
    childrenField: {type: String, default: 'children'},         // 
    getChildren: {type: Function},                              //  lazy true
    isCheckable: {type: Function},                              // 
    isLeaf: {type: Function},                                   // lazy
    filterNodeMethod: {type: Function},                         // truefalse
    checkStrictly: {type: Boolean},                             // false
    autoExpandParent: {type: Boolean, default: true},           // 
}

export type TablePropsType = ExtractPropTypes<typeof TableProps>

export const TABLE_PROVIDER = '@@TABLE_PROVIDER'

export const enum TableHoverPart {
    body = 'body',
    head = 'head'
}

export const enum TableCheckStatus {
    check = 'check',
    uncheck = 'uncheck',
    minus = 'minus',
}